.container {
  display: flex;
  justify-content: flex-start;
}

.caratula {
  width: 300px;
  height: 300px;
  background: lightgray;
}

.disco {
  width: 300px;
  height: 300px;
  background: #000;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.medio {
  width: 100px;
  height: 100px;
  background: red;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.hueco {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #fff;
}
